<template>
  <div class="nav-header">
    <van-nav-bar title="车牌号维护" left-text="返回" left-arrow @click-left="back"></van-nav-bar>
    <div class="con-main">
      <van-cell-group class="vcg-Inp">
        <van-field
          v-model="retStartDate"
          type="text"
          placeholder="请输入创建日期"
          input-align="right"
          readonly
        >
          <p slot="label" class="pos-r">
            创建日期
          </p>
        </van-field>
        <van-field
          v-model="plateInfoData.mayFinishDate"
          type="text"
          placeholder="请输入应完成日期"
          input-align="right"
          readonly
        >
          <p slot="label" class="pos-r">
            应完成日期
          </p>
        </van-field>
      </van-cell-group>
<!-- {{editOrView&&($route.query.approveStatus==1?false:true)}} -->
      <ul v-for="(item , index) in plateInfoData.vflSuperviseVehicleExtendList" :key="item.imgId">
        <p class="soltCars">车辆{{index+1}}</p>
        <li>
          <van-cell-group class="vcg-Inp inp-top">
            <!-- {{editOrView&&($route.query.approveStatus==1?true:false)}} -->
            <van-field
              v-model="item.pkModelName"
              :disabled="!editOrView"
              type="text"
              placeholder="请输入车辆型号"
              input-align="right"
            >
              <p slot="label" class="pos-r">
                车辆型号
                <span class="required" v-if="editOrView">*</span>
              </p>
            </van-field>
            <van-field
              v-model="item.plateNo"
              :disabled="!editOrView"
              type="text"
              placeholder="请输入要维护车牌号"
              input-align="right"
            >
              <p slot="label" class="pos-r">
                车牌号维护
                <span class="required" v-if="editOrView">*</span>
              </p>
            </van-field>
          </van-cell-group>
        </li>
      </ul>
    </div>
   
    <van-button
      v-if="editOrView"
      class="footer-btn"
      type="info"
      size="large"
      v-on:click="submits"
    >提交</van-button>
  </div>
</template>

<script>
import moment from "moment";
import { dateFormat } from "../assets/js/dateformat";
import "../assets/fonts/iconfont.css";
import global_config from "../api/globalConfig";

export default {
  name: "plate_maintenance",
  data() {
    return {
      err: {
        createErrMsg: "",
        mayFinishDate: ""
      },
      resCode: "",
      editOrView: false,
      plateInfoData: {
        id: "",
        createDate: "",
        mayFinishDate: "",
        vflSuperviseVehicleExtendList: [
          {
            id: "",
            vinCode: "",
            plateNo: "",
            imginfo: "",
            imgId: "",
            fileIdList: [],
            imgBase: "",
            imgFlag: true
          }
        ]
      },
    };
  },
  created() {
    if(this.$route.query.superviseStatus==1&&this.$route.query.approveStatus==1){
      this.editOrView=true
    }
  
    this.plateInfo(this.$route.query.item, this.$route.query.superviseStatus);
  },
  computed:{
    retStartDate:function(){
      let retStr="";
      if(this.plateInfoData.createTime){
        retStr=this.plateInfoData.createTime.split(' ')[0];
      }
      return retStr
    }
  },
  // beforeDestroy() {
  
  // },
  methods: {
    plateInfo: function(id, superviseStatus) {
      this.callServer({
        params: {
          reqmethod: "postjson",
          requrl: `/imfbp-vfl-app-web/vflSupervise/getVflSuperviseById`,
          reqparam: { id, superviseStatus }
        }
      })
        .then(res => {
          if (res.detailMsg.data) {
            this.createDateModel =
              new Date(res.detailMsg.data.createDate) || new Date();
            this.mayFinishDateModel =
              new Date(res.detailMsg.data.mayFinishDate) || new Date();
            this.plateInfoData = res.detailMsg.data;
          }
        })
        .catch(err => {
          // console.log(err);
        });
    },
    back() {
        this.$store.commit("setSupManTab", this.$route.query.superviseStatus);
      this.$router.push({
        name: "supervisory_interface",
        query: { superviseStatus: this.$route.query.superviseStatus }
      });
    },
    submits() {
    
        let isReturn=false
          // this.$toast("请选择创建日期");
          // return;
          // this.$toast("请选择应完成日期");
          // return;
          this.plateInfoData.vflSuperviseVehicleExtendList.map(r => {
            if (!r.pkModelName) {
              this.$toast("请选择车辆型号");
              isReturn=true
            } else if (!r.plateNo) {
              this.$toast("请选择要维护车牌号");
              isReturn=true
            }
          });
          // this.$toast("请选择应完成日期");
        if(isReturn) return;
          let self = this;
          // console.log(self.plateInfoData, "self.plateInfoData");
          self.$toast.loading({
            duration: 0,
            forbidClick: true, // 禁用背景点击
            message: "提交中..."
          });
          self
            .callServer({
              params: {
                reqmethod: "get",
                requrl:
                  "/eiap-plus/appResAllocate/queryBpmTemplateAllocate?funccode=VFL0503&nodekey=VFL0503&_=" +
                  moment().valueOf(),
                reqparam: ""
              }
            })
            .then(res => {
              // console.log("submitCode", res);
              if (res.detailMsg.data.res_code) {
                self.resCode = res.detailMsg.data.res_code;
                let idList = [{ id: this.$route.query.item }];
                // self.plateInfoData.superviseStatus = "2";
                // self.plateInfoData.vflSuperviseVehicleExtendList.fileIdList=[]
                self
                  .callServer({
                    params: {
                      reqmethod: "postjson",
                      requrl:
                        "/imfbp-vfl-app-web/vflSuperviseBpm/saveAndSubmit?processDefineCode=" +
                        res.detailMsg.data.res_code,
                      reqparam: self.plateInfoData
                    }
                  })
                  .then(res => {
                    if (res.success) {
                      self.$toast(res.message);
                      self.$router.push({
                        name: "supervisory_interface",
                                query: { superviseStatus: self.$route.query.superviseStatus }
                      });
                    }
                  })
                  .catch(err => {
                    self.$toast("请求失败");
                    self.$toast.clear();
                  });
              } else {
                self.$toast("单据没有关联流程!");
              }
            })
            .catch(err => {
               self.$toast("无法获取code值")
              setTimeout(self.$toast.clear(),500)
              // self.$toast.clear();
              console.log(err);
            });
      
    },
  }
};
</script>

<style scoped>
.soltCars {
  font-size: 13px;
  color: #2d2d2d;
  margin-left: 16px;
  padding-top: 5px;
}

.con-main {
  margin-bottom: 50px;
}

.ocr-box {
  display: flex;
  padding: 30px 0 10px;
}

.ocr-row {
  flex: 1;
}

.ocr-row-1 {
  width: 64px;
  height: 64px;
  background: #f0f0f5;
  text-align: center;
  line-height: 64px;
  border-radius: 50%;
  margin: 0 auto;
}

.iconzj-Album {
  font-size: 30px;
  color: rgb(255, 169, 65);
}

.iconzj-shot {
  font-size: 30px;
  color: rgb(50, 150, 250);
}

.ocr-row-2 {
  font-size: 14px;
  color: #2d2d2d;
  height: 30px;
  line-height: 30px;
  width: 100%;
  text-align: center;
}

.img-info-data {
  background: #eeeff3;
  width: 164px;
  height: 117px;
  position: relative;
  margin: 16px;
}

.img-pic {
  margin: 11px 8px 0 8px;
  width: 148px;
  height: 101px;
}

.tip-con {
  position: absolute;
  bottom: 0px;
  width: 100%;
  background: #000;
  height: 30px;
  opacity: 0.46;
  border-radius: 0px 0px 2px 2px;
  text-align: center;
  line-height: 30px;
}

.tip-text {
  color: #fff;
  font-size: 13px;
}

.footer-btn {
  position: fixed;
  bottom: 0;
  left: 0;
}

.ocrIcon {
  border-left: 1px solid #ccc;
  display: block;
  width: 40px;
  margin-left: 16px;
  /* position: relative;
        position: absolute; */
}
</style>